<template>
    <v-header>
        <div slot="right" :class="['h-right']" v-if="status==2"><a
                v-link="{name:'certificate-edit',params:{id:id},query:{isNew:false}}" class="mg-r10">编辑</a></div>
    </v-header>
    <v-content>
        <div class="certification-box">
            <div class="img status status{{status}}"></div>
            <div class="section">
                <div class="matter">
                    <p class="title">认证技能</p>
                    <div class="edit">
                        <p class="text">{{categoryName}}</p>
                    </div>
                </div>
            </div>

            <div class="section">
                <div class="matter">
                    <p class="title">证书名称</p>
                    <div class="edit">
                        <p class="text">{{name}}</p>
                    </div>
                </div>
                <div class="matter">
                    <p class="title">证书编号</p>
                    <div class="edit">
                        <p class="text">{{code}}</p>
                    </div>
                </div>
                <div class="matter">
                    <p class="title">有效期限</p>
                    <div class="edit">
                        <p class="text">{{deadLine}}</p>
                    </div>
                </div>
                <div class="matter">
                    <p class="title">颁发机构</p>
                    <div class="edit">
                        <p class="text">{{org}}</p>
                    </div>
                </div>
                <div class="pic-area">
                    <p class="title">证书照片</p>
                    <div class="pic-view">
                        <div v-for="item in items" :class="[$index % 3 == 0 ? 'first':'']" style="width:{{picSize}}px;height:{{picSize}}px;
                        background-image:url({{item.imgPath | img-host}});" @click="fullView($index)">
                        </div>
                    </div>
                </div>
            </div>
            <div @click="deleteCert" class="delete-cert" v-if="status==2 || status==3">删除认证</div>
        </div>
    </v-content>
    <v-pic-view></v-pic-view>
</template>
<style scoped>
    .section .matter {
        border-bottom: none;
    }

    .status {
        background-image: url(../../../assets/img/icon/seals.png);
        width: 65px;
        height: 65px;
        position: absolute;
        top: .5rem;
        right: .1rem;
        background-size: 260px 65px;
    }

    .status0 {
        background-position: -65px 0;
    }

    .status1 {
        background-position: 0 0;
    }

    .status2 {
        background-position: -130px 0;
    }

    .status3 {
        background-position: -195px 0;
    }

    .button.delete {
        color: #999999;
    }

    .delete-cert {
        background-color: #fff;
        color: #999;
        margin-top: .2rem;
        line-height: .45rem;
        text-align: center;
        font-size: .16rem;
    }

    .pic-area {
        padding: .15rem .1rem .25rem;
        background-color: #fff;
        overflow: hidden;
    }

    .pic-area .title {
        font-size: .16rem;
        margin-bottom: .1rem;
    }

    .pic-view > div {
        position: relative;
        margin-left: .05rem;
        margin-top: .05rem;
        float: left;
        background-size: 100% 100%;
    }

    .pic-view > div:first-child {
        margin-left: 0;
    }

    .pic-view > div.first {
        margin-left: 0;
    }

    .pic-view > div:not(:last-child):after {
        content: '';
        position: absolute;
    }
</style>
<script>
    import {url, lt} from '../../../const'
    import {imgHost} from '../../../filter'
    import {toast} from '../../../assets/js/toast'
    import {DJUtils} from '../../../assets/js/common'
    import VHeader from '../../../components/Header'
    import VFooterBtn from '../../../components/FooterBtn'
    import VContent from '../../../components/Content'
    import VPicView from '../../../components/PicView'
    import $ from 'jquery'
    export default{
        route: {
            data(){
                let vm = this;
                vm.review = false;
                let size = $(window).width() - 20 - 5 * 2;
                vm.picSize = size / 3;
                vm.$broadcast('reset-pic-view');
                let id = vm.$route.params.id;
                this.$http.get(url.getCertDetail, {id: id}).then(
                    function ({data:{success, data, resultMsg}}) {
                        if (success) {
                            vm.buildData(data);
                        } else {
                            toast.alert(resultMsg);
                        }
                    }, function (response) {
                        toast.alert('网络异常');
                    }
                );
            }
        },
        data(){
            return {
                id: 0,
                name: '',
                category: 0,
                categoryName: '',
                code: '',
                deadLine: '',
                org: '',
                status: 0,

                picSize: 0,
                items: [],
                review: false
            }
        },
        ready(){

        },
        methods: {
            fullView(index){
                let list = this.items;
                let items = [];
                let link = '';
                for (let i = 0; i < list.length; i++) {
                    link = list[i].imgPath;
                    link = link.split('.').join('big.');
                    items.push({
                        id: list[i].id,
                        link: link
                    });
                }
                let data = {items: items, index: index, review: this.review};
                this.$broadcast('pic-view', data);
                this.review = true;
            },
            buildData (item){
                if (item) {
                    this.id = item.id;
                    this.name = item.name;
                    this.category = item.category;
                    this.categoryName = item.categoryName;
                    this.code = item.code;
                    this.deadLine = item.deadLine;
                    this.org = item.org;
                    this.status = item.status;
                    this.items = item.items;
                }
            },
            deleteCert (){
                let vm = this;
                toast.conform('确定删除?', function () {
                    vm.$http.get(url.deleteCertification, {id: vm.id}).then(
                        function ({data:{success, data, resultMsg}}) {
                            if (success) {
                                toast.alert("删除成功");
                                DJUtils.backHistory();
                            } else {
                                toast.alert(resultMsg);
                            }
                        }, function (response) {
                            toast.alert('网络异常');
                        }
                    );
                });

            }
        },
        components: {VHeader, VFooterBtn, VContent, VPicView}
    }
</script>



